<template>
    <div class="Indetail">
      <div class="liucheng" v-if="detail.type==6||detail.type==7">
          <li v-for="(item,index) in liuList" :key="index">
            <div class="liuTime" v-if="item.date">
              <div :class="item.date?'lian':''">
                <div class="xian"></div>
                <div class="xian"></div>
              </div>
              <div :class="item.date?'timeDetail':''">
                  {{item.date}}
              </div>
            </div>
            <div class="status">
              <div class="lian">
                <div class="xian"></div>
                <div class="dian"  :class="item.date?'dianbig':''"></div>
                <div class="xian"></div>
              </div>
              <div class="statusDetail" :class="item.date?'statusDetails':''">
                {{item.status}}
              </div>
            </div>
          </li>
        <div class="imginfo">
          <img src="../assets/img/shouyixiangqing--renwu.png" alt="">
        </div>
      </div>
      <div class="IncomeDetail">
         <div class="jiner">
           <span class="info">积分</span><div><span>{{detail.data_type===1?'+':'-' +  detail.points}}</span></div>
         </div>
        <div class="type">
          <span class="info">收入类别</span>
          <div>
            <span v-if="detail.type==7">首佣</span>
            <span v-if="detail.type==6">续佣</span>
            <span v-if="detail.type==5">增员奖</span>
            <span v-if="detail.type==4">管理奖</span>
            <span v-if="detail.type==3">充值</span>
            <span v-if="detail.type==8">兑换</span>
          </div>
        </div>
        <div class="time">
          <span class="info">时间</span><div><span>{{detail.inserve_data.created_at}}</span></div>
        </div>
        <div class="detail">
          <span class="info">详情</span>
          <div>
            <div v-if="detail.type==3||detail.type==8">
              <div>{{detail.name}}</div>
              <div v-if="detail.type==3">￥{{detail.inserve_data.money}}</div>
            </div>
            <div v-else>
              <div>{{detail.name}} <span v-if="detail.type==5">({{detail.inserve_data.job_type}})</span></div>
              <div v-if="detail.type!=4">{{detail.inserve_data.insu_id}}<span v-if="detail.type==6">({{detail.inserve_data.remark}})</span></div>
              <div>￥{{detail.inserve_data.total_fee }}</div>
            </div>
            <div>订单号：{{detail.inserve_data.order_sn}}</div>
          </div>
        </div>
      </div>
      <a href="" class="kefu iconfont icon-kefu"></a>
    </div>
</template>

<script>
import { apiGet,apiPost } from '../common/js/api';
    export default {
      name: "jf_detail",
      data(){
        const query = this.$route.query
        return{
          scoreId:query.scoreId,
          liuList:[
            {
              'date':"2018-8-22 14:00",
              "status":"预约成功"
            },
            {
              'date':"2018-8-22 14:00",
              "status":"已见面"
            },
            {
              'date':"",
              "status":"已签约"
            },
            {
              'date':"",
              "status":"已返款"
            }
          ],
          detail:{
            money:5000,
            typenName:"管理奖",
            date:"2018-08-23    10:00:00",
            info:"被他如何让他被他如何让他进入天津体育课被他如何让他进入天津体育课被他如何让他进入天津体育课进入天津体育课"
          }
        }
      },
      created(){
        this.getDetail()
      },
      methods:{
        //获取收益详情
        getDetail(){
          let formData = new FormData();
          formData.append("id",this.scoreId)
          formData.append("accessToken",this.$cookie.get('accessToken'))
          // formData.append("accessToken","9RVRcGJsjs7BzwvRTN5ZFgdQHleRj5z7")
          apiPost("/integral/integral-details",formData).then(res=>{
            const data = res.data.data.inserve_data;
            this.detail = res.data.data;
            this.liuList[0].date = data.yu_time
            this.liuList[1].date = data.jian_time
            this.liuList[2].date = data.created_at
            this.liuList[3].date = data.pay_time
            // this.detail = res.data.detail
          })
        }
      }
    }
</script>

<style scoped lang="scss">
  .Indetail{
      height: 100%;
    background: #E6E9ED;
      overflow: auto;
     padding-top: 0.18rem;
    .liucheng{
      width:3.5rem;
      min-height:2.42rem;
      padding-bottom: 0.2rem;
      background:linear-gradient(-90deg,rgba(127,173,249,1),rgba(70,133,238,1));
      border-radius:0.04rem;
      color: white;
      margin: 0 auto;
      position: relative;
      padding-top: 0.22rem;
      .imginfo{
        position: absolute;
        width: 0.86rem;
        height: 0.76rem;
        right: 0.63rem;
        bottom:0.55rem;
        img{
          width: 0.86rem;
          height: 0.76rem;
        }
      }
      li{margin-left: 0.28rem;
        .liuTime,.status{
          display: flex;
          width: 2rem;
          margin-bottom: 0.12rem;
        }
        .lian{
          height: 0.3rem;
          width: 0.16rem;
          position: relative;
          text-align: center;
          .dian{
            width: 0.04rem;
            height: 0.04rem;
            border-radius: 0.04rem;
            background: white;
            position: absolute;
            z-index: 3;
            top: 50%;
            left: 50%;
          }
          .dianbig{
            width: 0.08rem;
            height: 0.08rem;
            left: 40%;
            border-radius: 0.08rem;
          }
          .xian{
            height: 50%;
            width: 1px;
            top:0;
            color: rgba(255,255,255,.3);
            position: absolute;
            left: 60%;
            text-align: center;
            background: white;
          }
          .xian:last-of-type{
            height: 143%;
            width: 1px;
            left: 60%;
            top:60%;
            color: rgba(255,255,255,.3);
            position: absolute;
            text-align: center;
            background: white;
          }
        }
        .statusDetail{
          font-size: 0.17rem;
          color: rgba(255,255,255,.6);
        }
        .statusDetails{
          font-size: 0.19rem;
          color: white;
        }
        .statusDetail,.timeDetail{
          margin-left: 0.13rem;
          height: 0.3rem;
          line-height: 0.3rem;
        }
      }
      li:first-of-type{
        .liuTime{
          .lian{
           visibility: hidden
          }
        }
        .status{
          .lian{
            .xian:first-of-type{visibility: hidden}
          }
        }
      }
      li:last-of-type{
        .status{
          .lian{
            .xian:last-of-type{visibility: hidden}
          }
        }
      }
    }
    .IncomeDetail{
      width: 3.5rem;
      background: white;
      margin: 0.17rem auto;
      min-height: 1.34rem;
      border-radius: 0.04rem;
      padding-top: 0.23rem;
      &>div{
        margin-bottom: 0.1rem;
        padding:0 0.13rem;
        display: flex;
        justify-content: space-between;
        .info{
          font-size: 0.16rem;
          color: #333;
          font-weight: 600;
        }
        &>div{
          width: 2.3rem;
          display: block;
          text-align: right;
          font-size: 0.14rem;
          color: #9a9a9a;
        }
      }
    }
  }
</style>



